import React from "react";
import "../styles/Notfound.style.css";
import Helmet from "../commponents/Helmet/Helmet";


const Notfound = () => {
  return(
    <Helmet title={" oops  not found"}>
     <body className="notfound">
    <div class="text">
      <h2>404</h2>
      <h3>Sorry! Page Not Found</h3>
    </div>
    <div class="containerFounded">
      <div class="sand"></div>
      <div class="tower">
        <div class="pole"></div>
        <div class="door"></div>
        <div class="light-base"></div>
        <div class="dome"></div>
        <div class="light"></div>
      </div>
      <div class="sand-extra"></div>
    </div>
  </body>
  </Helmet>
  )
};


export default Notfound;